

.addTagModal .modal-dialog{
    max-width: calc(100vw - 130px);
    min-height: calc(100% - 3.5rem);
    justify-content: center;
}

.addTagModal .modal-content {
    max-height: 100%;
    max-width: 100%;
    position: relative;
    transition: all .25s;
    width: auto;
}

.addTagModal_headerLabel {
    display: none;
}

.addTagModal_closeBtn {
    position: absolute;
    right: -60px;
    top: 0;
    padding: 6px 8px;
    cursor: pointer;
}

.addTagModal_closeBtn svg {
    width: 18px;
    height: 18px;
}

.addTagModal_alignLeft {
    max-width: calc(100% - 400px);
    padding: 10px 0 10px 10px;
    position: relative;
}

.addTagModal_alignRight {
    border-radius: 0 10px 10px 0;
    width: 400px;
    padding: 20px;
}

.addTag_media_holder {
    background-color: #fff;
    flex-wrap: wrap;
    height: 100%;
    min-height: 30vh;
    position: relative;
    align-items: center;
    display: flex;
    position: relative;
    text-align: center;
    width: 100%;
}

.addTagPostImage{
    display: inline-block;
    max-height: calc(100vh - 70px);
    max-width: 100%;
    position: relative;
    transition: all .3s ease-in;
    vertical-align: middle;
    z-index: 1;
}

.addTags_btn.btn{
    width: 30px;
    height: 30px;
    line-height: 43px;
    padding: 0;
    border: 0;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.addTags_btn.btn i{
    font-size: 14px;
    color: #fff;
    vertical-align: middle;
    line-height: 1;
    display: inline-flex;
    padding: 0;
}

.addTags-list {
    margin-top: 20px;
}

.addTags-list_postTags{
    list-style: none;
    padding: 0;
    max-height: 350px;
    overflow-y: auto;
}

.addTags-list_postTags li{
    padding: 4px 10px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #f2f6f8;
    border-radius: 30px;
    border: 1px solid #ccc;
    box-shadow: 0 0 10px #f2f6f8;
    margin-bottom: 10px;
    margin-right: 5px;
}

.addTags-list_postTags li span {
    line-height: normal;
    font-size: 12px;
}

.addTags-list_postTags li a {
    margin-left: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.addTags-list_postTags li a i {
    font-size: 9px;
    color: #999;
    line-height: 0;
}

.addTagModal_alignRight .addtag_postTagsSugg .dropdown-menu-list .navi .image-tags .navi-link {
    padding: 5px 15px;
    font-size: 12px;
}

.addTags_card_cont{
    padding: 0;
}

.addTags_card_cont .symbol.symbol-circle{
    height: 30px;
    width: 30px;
    text-align: center;
    font-size: 22px;
    padding: 5px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #535353;
    color: #fff;
    display: block;
    font-size: 17px;
    font-weight: 500;
}

.addTags_user_content {
    max-width: 70%;
}

.addTags_post_title{
    font-size: 13px;
    color: #6D7175;
    font-weight: 600;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 215px;
}

.addTags_grid_userinfo {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 215px;
}

.addTags_grid_uername .addTags_timePost {
    line-height: 14px;
    font-size: 11px;
    color: #BABFC3;
    max-width: 110px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.sepratedot {
    height: 4px;
    width: 4px;
    background-color: #959595;
    border-radius: 50%;
    margin: 0 5px;
    display: table;
    align-self: center;
}

.addTags_des {
    font-size: 12px;
    max-height: calc(40vh - 150px);
    overflow-y: auto;
    min-height: 200px;
}

.addTag_image {
    height: 100%;
}

@media screen and (max-width:1199px){
    .addTags_card_cont {
        padding: 0;
        max-height: 60vh;
        overflow-y: auto;
    }
}


@media screen and (max-width:991px){

    .addTagModal .modal-dialog {
        max-width: 600px;
        min-height: 90vh;
    }

    .addTag_modal_inner{
        display: block !important;
    }

    .addTagModal_alignLeft {
        width: 100%;
        max-width: 100%;
        padding: 0;
    }

    .addTag_media_holder {
        display: block;
        min-height: 0px;
        height: auto;
    }

    .addTagPostImage {
        max-height: 100%;
        width: 100%;
    }

    .addTagModal_alignRight {
        width: 100%;
    }

}

@media screen and (max-width:767px){

    .addTagModal {
        padding: 0 !important;
        width: 100%;
    }

    .addTagModal .modal-dialog{
        display: block;
        /* height: 100vh; */
    }
    
    .addTagModal .modal-body {
        height: 100%;
    }

    .addTagModal .modal-content{
        border-radius: 0;
        height: 100%;
        display: block;
    }

    .addTagModal_head {
        background-color: #fff;
    }

    .addTagModal_header{
        align-items: center;
        border-bottom: 1px solid rgba(0,0,0,.1);
        display: flex;
        justify-content: space-between;
        padding: 0 15px;
        width: 100%;
    }

    .addTagModal_headerLabel{
        display: block;
    }

    .addTagModal_closeBtn {
        position: relative;
        right: 0;
    }

    .addTagModal_headerLabel {
        display: block;
        font-size: 14px;
        color: #2b2b2b;
    }

    .addTagModal_closeBtn svg path {
        fill: #2b2b2b;
    }

    .addTagModal_closeBtn svg {
        width: 14px;
        height: 14px;
    }

    .addTagModal .modal-dialog {
        max-width: 100%;
        margin: 0;
    }

}