.introjs-overlay { inset: 600px !important; }
    @media only screen and (max-width:1600px) and (min-width:991px) {
        .introjs-overlay { inset: 600px !important; }
    }

.logo-upload-section { max-width: 230px; }
.dashed_ugc_btn {
    height: 80px;
    overflow: hidden;
    border-radius: 4px;
    border: 2px dashed #999;
    width: 100%;
    display: block;
}

.ugc_upload_logo_btn p {
    display: block;
    width: 100%;
    margin: 0;
}

.ugc_upload_logo_btn {
    position: relative;
    height: 100%;
    text-align: center;
    cursor: pointer;
}

.ugc_upload_logo_btn .form-control {
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 0;
    cursor: pointer;
}