.slider_thumb img {
    height: 70px;
    width: 70px;
    display: flex;
    object-fit: cover;
    object-position: center;
}

.slider_thumb {
    border-radius: 4px;
    overflow: hidden;
}

.slide_thumbs_btn .btn{
    opacity: .6;
}

.slide_thumbs_btn .btn:hover{
    opacity: 1;
}

.slide_thumbs_btn .btn:hover .remove-thumbnail{
    visibility: visible;
    opacity: 1;
}

.slide_thumbs_btn .btn.active{
    opacity: 1;
}

.remove-thumbnail {
    cursor: pointer;
    background-color: red;
    border-radius: 50%;
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #fff;
    width: 16px;
    height: 16px;
    text-indent: 0;
    text-align: center;
    justify-content: center;
    position: absolute;
    right: -6px;
    top: -6px;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}

.btn span.remove-thumbnail i {
    font-size: 7px;
    color: #fff;
    margin: 0;
    padding: 0;
}

.ugc_arrow_ico i {
    font-size: 24px;
    color: #545454;
}

.ugc_arrow_btn{
    width: 40px;
    height: 40px;
    z-index: 3;
    opacity: 1;
    border-radius: 50%;
    top:calc(50% - 20px);
    background-color: #F2F2F2;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-box-shadow: 0 1px 2px rgb(0 0 0 / 6%), 0 2px 4px rgb(0 0 0 / 4%), 0 5px 14px rgb(0 0 0 / 2%);
    box-shadow: 0 1px 2px rgb(0 0 0 / 6%), 0 2px 4px rgb(0 0 0 / 4%), 0 5px 14px rgb(0 0 0 / 2%);
}

.carousel-indicators {
    position: relative;
    margin-right: 0;
    margin-left: 0;
}

.carousel-indicators .btn {
    box-sizing: content-box;
    flex: inherit;
    width: auto;
    height: auto;
    padding: 0;
    margin-right: 3px;
    margin-left: 3px;
    text-indent: -999px;
    cursor: pointer;
    background-color: #fff;
    background-clip: padding-box;
    border: 0;
    border-top: none;
    border-bottom: none;
    opacity: .5;
    transition: opacity .6s ease;
}

.ugc-thumbnail-inn .remove-thumbnail{
    position: absolute;
    right: -5px;
    top: -5px;
    display: none;
}

.ugc-thumbnail-inn:hover .remove-thumbnail{
    display: block;
}

