.pinPost_outer {
    padding: 15px 15px 10px 30px;
}

.pintotoppost_main {
    max-height: 516px;
    overflow-y: scroll;
    padding: 5px 0;
}

.pt_card_outer {
    position: absolute;
    left: -5px;
    top: -5px;
}

.pt_card_outer:before {
    position: absolute;
    content: "";
    background: #fff;
    width: 100%;
    height: 100%;
    left: 1px;
    top: 1px;
    border-radius: 100%;
    z-index: 0;
}

.pt_card_number{
    position: relative;
    background: #3699ff;
    color:#fff;
    display: block;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-radius: 100%;
    z-index: 1;
    font-size: 12px;
}

.pt_profile_img {
    height: 40px;
    width: 40px;
    text-align: center;
    font-size: 22px;
    padding: 5px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #535353;
    color: #fff;
    display: block;
}

.pintopdesc .trimPostContent {
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    word-break: break-all;
}

.pintotoppost_main .trimPostContent{
    font-size: 12px;
}

.pt_card_iamge {
    width: 100px;
    height: 100px;
    aspect-ratio: 1/1;
}

.pt_card_iamge img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 1/1;
}

.pt_Card_content .m_user_content {
    margin-bottom: 12px;
}

.pt_dragable_card {
    padding-right: 20px;
}

.pintopdesc .readMore_btn{
    position: absolute;
    right: 6px;
    bottom: -6px;
    background: #fff;
    padding: 5px 10px;
    cursor: pointer;
    white-space: nowrap;
}

.pintopdesc .trimPostContent.active .readMore_btn {
    position: relative;
    display: inline-block;
    bottom: 0;
}

.pintopdesc .trimPostContent.active {
    display: block;
    overflow: visible;
}

@media screen and (max-width:991px){
    .pintotop_modal .modal-dialog.modal-lg {
        max-width: 85%;
        margin: 0 auto;
    }
}

@media screen and (max-width:767px){
    .pintopdesc .readMore_btn{
        position: absolute;
        right: 0;
    }
}

@media screen and (max-width:575px){
    .pintotop_modal .modal-dialog.modal-lg {
        max-width: 95%;
    }
}

@media screen and (max-width:480px){
    .pinPost_outer {
        padding: 15px 10px 10px 15px;
        overflow-y: scroll;
        max-height: 535px;
    }

    .pintotoppost_main {
        max-height: 100%;
        overflow-y: hidden;
    }

    .pintotop_modal .modal-dialog.modal-lg {
        max-width: 100%;
    }

    .pt_card_iamge {
        width: 80px;
        height: 80px;
    }

    .pt_profile_img {
        height: 30px;
        width: 30px;
    }

    .pt_Card_content .m_user_content {
        margin-bottom: 5px;
    }

    .pt_Card_content .m_grid_author {
        padding-right: 10px;
        align-items: flex-start !important;
    }

    .pt_Card_content .m_post_title {
        align-items: flex-start;
        line-height: normal;
        display: flex;
        margin-bottom: 0px;
        position: relative;
        top: -2px;
    }

    .pt_Card_content .pt_author_outer {
        align-items: flex-start !important;
    }

    .pt_dragable_card {
        padding-right: 2px;
    }
}

@media screen and (max-width:359px){

    .pt_dragable_card {
        padding-right: 10px;
    }

    .pt_Card_content {
        max-width: 60%;
    }

    .pt_Card_content .m_user_content {
        max-width: 85%;
    }

    .pt_Card_content .m_grid_author{
        max-width:100%;
    }

    .pt_Card_content .pt_author_outer {
        max-width: 98%;
    }

    .pt_card_iamge {
        width: 60px;
        height: 60px;
    }

    .pintopdesc {
        max-width: 100%;
    }

    
}