/*==== UGC RIGHTS NEW CSS START ====*/

.ugc-modal, 
.ugc-modal .modal-dialog, 
.ugc-modal .modal-content{
    /* height: 100%; */
}

.ugc_approved.modal-body, .ugc_pending.modal-body {
    min-height: calc(100vh - 149px);
    overflow: hidden;
}

.ugc-modal .modal-header {
    padding: 0 10px !important;
}

.ugc-modal .modal-header .svg-icon-col svg {
    width: 23px;
}

.ugc-modal .modal-header .modal-title {
    font-size: 14px;
}

#ugcstatusUGC .ugc-modal .nav .nav-link {
    padding: 13px 0 !important;
    background: #f4f5fa;
    font-size: 13px;
    color: #343a40 !important;
}

.ugcCheck_icon {
    width: 18px;
    height: 18px;
    border-radius: 100%;
}

.ugcCheck_icon {
    width: 16px;
    height: 16px;
    border-radius: 100%;
    border: 1px solid #6c757d;
    color: #6c757d;
    padding: 2px;
    font-size: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    margin-right: 4px;
}

#ugcstatusUGC .nav .nav-link.active:before{
    display: none;
}

.rights_modal_sidebar .ugc-wizard-nav .nav.nav-tabs .nav-link{
    border-bottom: none;
}

#ugcstatusUGC .nav .nav-link.active .ugcCheck_icon {
    background: #50cd89;
    border-color: #50cd89;
    color: #fff;
}

.rights_modal_sidebar {
    border-left: 1px solid #e1e3ea;
}

.rights_modal_inner {
    height: auto;
    min-height: calc(100vh - 46px);
    width: calc(100% - 500px);
}

.ugc_card_preview.ugc_modal_pending_first_box{
    width: calc(100% - 500px);
}

.rights_modal_sidebar {
    max-width: 500px;
    min-height: calc(100vh - 46px);
}

.ugc_col_right{
    max-width: 500px;
}

.ugc_steps label.btn.btn-outline-secondary {
    border: 1px solid #ccc;
    outline: none !important;
    box-shadow: none;
    padding: 16px 14px;
}

.ugc_steps .btn.btn-outline-secondary.border-success{
    border-color: #6ac259 !important;
}

.ugc_steps label.btn.btn-outline-secondary .fs-10 {
    font-size: 11px;
    line-height: 14.735px;
}

.ugc_steps label.btn.btn-outline-secondary .w-20px {
    width: 20px!important;
}

.ugcCheck_icon.text-success {
    border-color: #6ac259;
    background: #6ac259;
    color: #fff !important;
}

.generalrequest-tabContent h3{
    font-size: 14px;
    font-weight: 600;
}

.generalrequest-tabContent p{
    font-size: 13px;
    font-weight: 400;
    margin-bottom: 16px;
    line-height: 17px;
}

.ugc_steps label.btn.btn-outline-secondary p {
    font-size: 14px !important;
    font-weight: 500;
    line-height: 1.45;
}

.fs-7 {
    font-size: calc(11px + .1vw + .1vh)!important;
}

.ugc_steps textarea.form-control.form-control-solid {
    min-height: 180px!important;
    background-color: #fff;
    border-radius: 6px;
    border-color: #d5d5d5;
}

.textarea-min-row-8.form-control-lg{
    font-size: 14px;
}

@media screen and (max-width:1366px){
    .rights_modal_inner,
    .ugc_card_preview.ugc_modal_pending_first_box {
        width: calc(100% - 400px);
    }

    .rights_modal_sidebar, .ugc_col_right {
        max-width: 400px;
    }

    #ugcstatusUGC .ugc-modal .nav .nav-link {
        font-size: 12px;
    }

    #ugcPostSlider {
        max-width: 400px;
    }

    .ugc_card_preview .splide {
        max-width: 400px;
    }

    .card_slider .m_card_cont {
        min-width: 100%;
    }
}

@media screen and (max-width:991px){
    .rights_modal_inner {
        width: 100%;
        height: auto;
        min-height: 100%;
    }

    .rights_modal_inner, 
    .rights_modal_sidebar, 
    .ugc_card_preview.ugc_modal_pending_first_box,
    .ugc_col_right {
        height: auto;
        width: 100%;
        min-height: 0;
    }

    .col.ugc_col_left {
        flex: auto;
    }
    .ugc_card_preview .splide {
        max-width: 100%;
    }

    .ugc-modal {
        overflow-y: auto;
    }

    .ugc-modal .modal-dialog{
        box-shadow: none;
    }

    .ugc-modal{
        position: fixed;
        height: 100%;
    }

    .ugc-modal .modal-content {
        height: auto;
    }

    .rights_modal_sidebar,
    .ugc_col_right {
        max-width: 100%;
        margin-top: 40px;
    }

}





/*==== UGC RIGHTS NEW CSS END ====*/