.tagProduct_modal .modal-dialog {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    pointer-events: none;
    z-index: 1;
    opacity: 1;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    max-width: calc(100vw - 130px);
    min-height: calc(100% - 3rem);
    justify-content: center;
    margin: 0 auto;
    height: 100%;
    padding: 20px 70px;
}

.tagProduct_modal .tagProduct_modal_head {
    border: none;
}

.tagProduct_modal_close_wrap {
    position: absolute;
    right: -70px;
    top: 0;
    z-index: 9;
}

.tagProduct_modal_close_btn{
    border: none;
    border-radius: 4px;
    display: inline-block;
    background-color: transparent;
    padding: 6px 4px;
    line-height: 0;
    transition: all .3s;
    font-size: 7px;
    position: relative;
}

.tagProduct_modal_close_btn svg {
    height: 28px !important;
    width: 28px !important;
}

.tagProduct_modalBody{
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    box-shadow: 0 0.25rem 0.5rem rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
    flex: 1 1 auto;
}

.tagProduct_modal .modal-content {
    max-height: 100%;
    max-width: 100%;
    position: relative;
    transition: all .25s;
    width: auto;
}

.tagProduct_leftAlign {
    max-width: calc(100% - 400px);
    padding: 10px 0 10px 10px;
    position: relative;
}

.tagProduct_media_holder {
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    min-height: 30vh;
    position: relative;
    align-items: center;
}

.tagProduct_image_holder{
    position: relative;
    width: 100%;
}

/* .tagProduct_image {
    display: inline-block;
    max-height: calc(100vh - 201px);
    max-width: calc(100vw - 400px);
    transition: all .3s ease-in;
    vertical-align: middle;
    min-height: calc(100vh - 62px);
} */

.tagProduct_image {
    display: inline-block;
    max-height: calc(100vh - 70px);
    max-width: 100%;
    position: relative;
    transition: all .3s ease-in;
    vertical-align: middle;
    z-index: 1;
}

.tagProduct_rightAlign{
    width: 400px;
    padding: 0 0 10px;
}

.tagProduct_content_wrap{
    width:100%;
}

.tagProduct_proSearch{
    padding: 0 30px;
    margin-bottom: 10px;
    margin-top: 20px;
    width: 100%;
}

.tagProduct_title{
    font-size: 14px;
}

.tagProduct_proSearch label{
    font-size: 12px;
}

.tagProduct_proSearch .input-group{
    background-color: #fff;
    border-radius: 0.42rem;
    border: 1px solid #dcdddf;
}

.tagProduct_proSearch .input-group .form-control{
    height: calc(1.2em + 1.3rem);
    padding: 0 10px;
    font-size: 12px;
}

.input-group.input-group-solid .form-control {
    border: 0;
    background-color: transparent;
    outline: 0 !important;
    box-shadow: none;
}

.input-group.input-group-solid .input-group-append, 
.input-group.input-group-solid .input-group-prepend, 
.input-group.input-group-solid .input-group-text {
    background-color: transparent;
    border: 0;
    padding-top: 0;
    padding-bottom: 0;
}

.tagProduct_searchBox .svg-icon.svg-icon-sm svg {
    height: 1rem!important;
    width: 1rem!important;
}

.tagProduct_searchBox .input-group.input-group-solid .form-control {
    height: calc(1.2em + 1.3rem);
    padding: 0 10px;
    font-size: 12px;
}

.tagProduct_searchBox .tagProduct_selected_tags {
    font-size: 12px;
    background: #f1f1f1;
    border-radius: 4px !important;
    padding: 4px 2px 4px 6px;
    display: flex;
    align-items: center;
    line-height: 0;
    margin-right: 5px;
    margin-bottom: 5px;
}

.tagProduct_searchBox .tagProduct_selected_tags:last-child{
    margin-right: 0;
}

.tagsOuterWrap {
    display: flex;
    align-items: center;
    justify-content: start;
    flex-wrap: wrap;
    padding: 5px;
    width: 100%;
}

.tagProduct_close_btn{
    background-color: initial;
    border: none;
    border-radius: 4px;
    display: inline-block;
    font-size: 7px;
    line-height: 0;
    padding: 0;
    position: relative;
    transition: all .3s;
}

.tagProduct_close_btn svg {
    height: 12px !important;
    width: 12px !important;
    margin-left: 4px;
}

.tagProduct_modal_close_btn g {
    fill: #fff;
}

.tagProduct_sideContent {
    max-height: calc(100vh - 40vh);
    overflow-y: auto;
}

.tagProduct_shop_cont {
    margin: 10px auto;
    width: 100%;
    padding: 0px 30px 30px;
    min-height: 45px;
    margin: 0;
}

.tagProduct_pro_wrap_ {
    margin-bottom: 10px;
}

.tagProduct_product_card, .tagProduct_product_card .symbol .symbol-label {
    border-radius: 3px;
    cursor: pointer;
}

.tagProduct_count {
    position: absolute;
    left: -14px;
    background-color: #2b2b2b;
    z-index: 1;
    border-radius: 50%;
    height: 18px;
    width: 18px;
    color: #fff;
    text-align: center;
    font-size: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tagProduct_product_card .symbol .symbol-label img {
    height: 100%;
    object-fit: contain;
}

.tagProduct_contentAlignRight{
    width: calc(100% - 55px);
}

.tagProduct_proTitle {
    max-width: 85%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 13px;
}

.tagProduct_proPrice {
    color: #313030;
    font-size: 11px;
}

.tagProduct_productPrice {
    text-decoration: line-through;
}

.btn.tagProduct_pro_delete .svg-icon svg g [fill] {
    fill: #959595;
}

.tagProduct_post_author {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 30px 0;
    width: 100%;
}

.tagProduct_author_details {
    display: flex;
    align-items: center;
    width: 90%;
}

.tagProduct_author_media{
    border-radius: 50%;
    border: 1px solid rgba(0,0,0,0.2);
    overflow: hidden;
    width: 35px;
    height: 35px;
    margin-right: 10px;
    display: flex;
    flex-shrink: 0;
    background-color: #fff;
}

.tagProduct_author_media .symbol.symbol-circle {
    height: 100%;
    width: 100%;
    text-align: center;
    font-size: 22px;
    padding: 5px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #535353;
    color: #fff;
    display: block;
}

.tagProduct_author_deatils {
    display: flex;
    align-items: center;
    justify-content: start;
    width: 80%;
    flex-wrap: wrap;
}

.tagProduct_author_usrname {
    font-size: 13px;
    font-weight: 600;
    color: #262626;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: block;
    line-height: normal;
    margin-bottom: 2px;
}

.tagProduct_author_info {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 100%;
}

.tagProduct_post_time, 
.tagProduct_author_handlename {
    color: #959595;
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
}

.tagProduct_author_handlename {
    white-space: nowrap;
    overflow: hidden;
    max-width: 60%;
    text-overflow: ellipsis;
    color: #262626;
}

.tagProduct .seprator_dot {
    height: 4px;
    width: 4px;
    background-color: #262626;
    border-radius: 50%;
    margin: 0 5px;
    align-self: center;
    flex-shrink: 0;
    display: flex;
}

.tagProduct_post_time {
    white-space: nowrap;
    overflow: hidden;
    max-width: 30%;
    text-overflow: ellipsis;
    color: #262626;
    line-height: normal;
    font-weight: 400;
    font-size: 10px;
}

.tagProduct_social_icon {
    display: flex;
    justify-content: center;
    margin: 0 auto;
}

.tagProduct_social_icon i {
    font-size: 26px;
}

.tagProduct_post_wrapper {
    padding: 15px 30px;
    height: auto;
    overflow: hidden;
}

.tagProduct_post_wrapper > div {
    overflow-x: hidden !important;
    margin-bottom: 0px !important;
}

.tagProduct_post_content {
    margin-bottom: 20px;
    word-break: break-word;
    font-size: 13px;
    color: #545454;
    padding-top: 5px;
    padding-bottom: 15px;
}

.chtrLimits, .read-more-container {
    display: inline;
    white-space: pre-line;
}

.chtrLimits {
    font-size: 11px;
}
.tagProduct_leftAlign video , .tagProduct_leftAlign iframe {
    display: inline-block;
    max-height: calc(100vh - 70px);
    max-width: 100%;
    position: relative;
    transition: all .3s ease-in;
    vertical-align: middle;
    z-index: 1;
    height: 100%;
}

@media screen and (max-width:1199px) and (min-width:992px){

    /* .tagProduct_leftAlign {
        max-width: calc(100% - 300px);
    }

    .tagProduct_rightAlign{
        width:300px
    } */

    .tagProduct_modal .modal-dialog{
        max-width: 100%;
    }

    .tagProduct_proSearch,
    .tagProduct_shop_cont{
        padding: 0 20px;
    }

    .tagProduct_post_author {
        padding: 15px 20px 0;
    }

    .tagProduct_post_wrapper{
        padding: 15px 20px;
    }
}

@media screen and (max-width:991px){

    .tagProduct_modal .modal-dialog, 
    .tagProduct_modal .modal-content,
    .tagProduct_modalBody {
        display: block;
        height: auto;
    }

    .tagProduct_modalBody {
        border-radius: 0 !important;
    }

    .tag_product_popup .modal-content{
        border-radius: 0 ;
    }

    .tagProduct_leftAlign {
        max-width: 100%;
        padding: 0;
        width: 100%;
    }

    .tagProduct_media_holder {
        width: 100%;
        min-height: 0;
    }

    .tagProduct_image {
        width: 100%;
        height: auto;
        max-height: 100%;
    }

    .tagProduct_rightAlign {
        width: 100%;
    }


}


@media screen and (max-width:767px){

    .tagProduct_modal{
        padding: 0 !important;
    }

    .tagProduct_modal .modal-dialog {
        padding: 0;
        width: 100%;
        max-width: 100%;
    }

    .tagProduct_modal .modal-content{
        border-radius: 0;
    }

    .tagProduct_modal_close_wrap {
        position: relative;
        right: 0;
    }

    .tagProduct_modal_close_btn svg {
        height: 20px !important;
        width: 28px !important;
    }

    .tagProduct_modal_close_btn g {
        fill: #2b2b2b;
    }

    .tagProduct_modal .tagProduct_modal_head {
        border: none;
        border-radius: 0 !important;
        justify-content: right;
        border-bottom: 1px solid #e1dfdf;
    }
    
}





